.App {
  position: absolute;
  left: 10%;
}

.Game {
  clear: both;
  padding-top: 5px;
}

.Timer {
  color: gray;
  float: left;
}

.Assistant {
  right: 0;
  display: inline-block;
  float: right;
}

.Overlay {
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: rotate(-10deg);
      -ms-transform: rotate(-10deg);
          transform: rotate(-10deg);
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  line-height: 2em;
  font-size: 1em;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;
  font-weight: bold;
  background: black;
}

.Row {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 0 1 auto;
      flex: 0 1 auto;
  -ms-flex-direction: row;
      flex-direction: row;
  border-top: 0px solid transparent;
}

.Field {
  display: inline-block;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 20px;
  height: 20px;
  line-height: 20px;
  border: 1px solid transparent;
}

.SixthRow .ValueField {
  border-top: 1px solid gray;
}

.SixthField {
  border-left: 1px solid gray;
}

.ValueField {
  outline: 1px solid gray;
  background: white;
}

.Hint.UserComplete,
.EmptyHint.UserComplete {
  opacity: 0.3;
}

.Hint {
  font-size: 11px;
}

.EmptyHint {
  background: white;
}


